import React, { useState, useEffect, Suspense, lazy } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { useSelector, useDispatch } from "react-redux";
import { ToastContainer } from "react-toastify";

import Topnav from "./Components/Topnav";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
import ScrollToTop from "./Components/ScrollToTop";
import Loader from "./Components/Loader";

import { apiGet } from "./utilities/axiosInstance"; // Import API utility
import { startLoading, finishLoading } from "./redux/features/loader/loaderSlice";
import API_ENDPOINTS from "./utilities/apiEndPoints";
import BookNow from "./Components/BookNow";

// Lazy load route components
const UserRoute = lazy(() => import("./Routing/UserRoute/UserRoute"));
const AboutUs = lazy(() => import("./Components/AboutUs"));
const Faq = lazy(() => import("./Components/Faq"));
const TestPackage = lazy(() => import("./Components/TestPackage"));
const Support = lazy(() => import("./Components/Support"));
const Search = lazy(() => import("./Components/Search"));
const PrivacyPolicy = lazy(() => import("./Components/PrivacyPolicy"));
const LearnersTerms = lazy(() => import("./Components/LernersTerms"));
const InstructorTerm = lazy(() => import("./Components/InstructorTerm"));
const LearnerLogin = lazy(() => import("./Components/LearnerLogin"));
const InstructorLogin = lazy(() => import("./Components/InstructorLogin"));
const ForgetPassword = lazy(() => import("./Components/ForgetPassword"));
const InstructorRegistration = lazy(() => import("./Components/Registration"));
const Thankyou = lazy(() => import("./Components/Thankyou"));
const PaymentPolicy = lazy(() => import("./Components/PaymentPolicy"));
const SearchResult = lazy(() => import("./Components/SearchResult"));
const PDP = lazy(() => import("./Components/PDP"));
const NotFound = lazy(() => import("./Components/NotFound"));

function App() {
  const { loading } = useSelector((state) => state.loader);
  const dispatch = useDispatch();
  
  // State to store navigation menu data
  const [navMenuData, setNavMenuData] = useState([]);

  // Fetch menu data from API
  const fetchMenuData = async () => {
    try {
      dispatch(startLoading());
      const response = await apiGet(API_ENDPOINTS.NAV_MENU);
      if (response && response.data) {
        setNavMenuData(response.data);
      }
      dispatch(finishLoading());
    } catch (error) {
      console.error("Error fetching menu data:", error);
      dispatch(finishLoading());
    }
  };

  useEffect(() => {
    fetchMenuData(); // Fetch menu data on component mount
  }, []);

  return (
    <>
     {/* <UserRoute/> */}
      {/* <AdminRoute/> */}
      <Router>
        <Suspense fallback={<div>{/* Loading... */}</div>}>
          <ScrollToTop />
          <Topnav />
          {/* Pass navMenuData to Header as a prop */}
          <Header navMenuData={navMenuData} />
          <Routes>
          <Route path="/" element={<UserRoute />} />
            <Route path="/about-us" element={<AboutUs />} />
            <Route path="/faqs" element={<Faq />} />
            <Route path="/test-package" element={<TestPackage />} />
            <Route path="/support" element={<Support />} />
            <Route path="/search" element={<Search />} />
            <Route path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route path="/learners-terms" element={<LearnersTerms />} />
            <Route path="/instructor-terms" element={<InstructorTerm />} />
            <Route path="/learner-login" element={<LearnerLogin />} />
            <Route path="/instructor-login" element={<InstructorLogin />} />
            <Route path="/forget-password" element={<ForgetPassword />} />
            <Route path="/registration" element={<InstructorRegistration />} />
            <Route path="/thankyou" element={<Thankyou />} />
            <Route path="/payment-policy" element={<PaymentPolicy />} />
            <Route path="/search-result" element={<SearchResult />} />
            <Route path="/profile-details" element={<PDP/>}/>
            <Route path="/book-now" element={<BookNow/>}/>
            <Route path="*" element={<NotFound />} />
            
          </Routes>
          <Footer navMenuData={navMenuData} />
        </Suspense>
      </Router>
      {loading && <Loader />}
      <ToastContainer />
    </>
  );
}

export default App;
