function Step2({ formData, handleChange }) {
    return (
      <div>
        <h3>Step 2: Contact Information</h3>
        <label>
          Address:
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          Phone:
          <input
            type="tel"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
          />
        </label>
      </div>
    );
  }
  
  export default Step2;
  