import { createSlice } from "@reduxjs/toolkit";

// Define initial state here
const initialState = {
  transmission: "manual",
  suburbArea: "",
};

const searchInstructorSlice = createSlice({
  name: "searchInstructor",
  initialState,
  reducers: {
    searchInstructor: (state, action) => {
      const { transmission, suburbArea } = action.payload;
      state.transmission = transmission;
      state.suburbArea = suburbArea;
    },

    clearState: (state) => {
      return initialState; // Reset to initial state
    },
  },
});

export const { searchInstructor, clearState } = searchInstructorSlice.actions;

export default searchInstructorSlice.reducer;
