import React from "react";
import { Container, Row, Col } from 'react-bootstrap'; 
import { Link } from "react-router-dom";

function Topnav() {
  return (

      <div className="Topnav">
        <Container>
        <Row>
            <Col lg={12}>
                <div className="Topnav__wrp">
                <Link to="/learner-login" className="btn btn-sm btn-white btn-bold ">
                    LEARNERS LOGIn
                </Link>
                <Link to="/instructor-login" className="btn btn-sm btn-white btn-bold ">
                    INSTRUCTORS LOGIN
                </Link>
                </div>
            </Col>
        </Row>
      </Container>
      </div>

  );
}

export default Topnav;
