import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/images/logo.svg";

function Header({ navMenuData }) {
  const [menuActive, setMenuActive] = useState(false);

  const toggleMenu = () => {
    setMenuActive(!menuActive);
  };

  const closeMenu = () => {
    setMenuActive(false);
  };

  return (
    <div className={`Header ${menuActive ? "menu-active" : ""}`}>
      <Container>
        <Row>
          <Col lg={12}>
            <div className="Header__wrp">
              <div className="Header__logo">
                <Link to="/" onClick={closeMenu}>
                  <img src={logo} alt="Logo" />
                </Link>
              </div>

              <ul className={`Header__menu ${menuActive ? "active" : ""}`}>
                {navMenuData && navMenuData.length > 0 ? (
                  navMenuData.map((item, index) => (
                    <li key={index}>
                      <Link to={`/${item.slug}`} onClick={closeMenu}>
                        {item.title}
                      </Link>
                    </li>
                  ))
                ) : (
                  <li>No Menu Items Available</li>
                )}
              </ul>

              {/* Hemburger icon - visible only on mobile screens */}
              <div className="Header__hemburger" onClick={toggleMenu}>
                {menuActive ? (
                  <span className="close-icon"></span>
                ) : (
                  <span className="hemburger-icon"></span>
                )}
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Header;
