function Step1({ formData, handleChange }) {
  return (
    <div className="bookNow__stepWrap d-flex">
      <div className="bookNow__stepWrapLeft">
        <h2>Select Lesson Type</h2>
      </div>
      <div className="bookNow__stepWrapRight"></div>

      <div className="d-none">
        <h3>Step 1: Personal Information</h3>
        <label>
          Name:
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
          />
        </label>
        <br />
        <label>
          Email:
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
        </label>
      </div>
    </div>
  );
}

export default Step1;
