// reducers/index.js
import { combineReducers } from "redux";
import loaderReducer from "../features/loader/loaderSlice";
import toastifyReducer from "../features/toastify/toastifySlice";
import userReducer from "../features/user/userSlice";
import searchInstructorReducer from "../features/searchInstructor/searchInstructorSlice";

const rootReducer = combineReducers({
  loader: loaderReducer,
  toastify: toastifyReducer,
  user: userReducer,
  searchInstructor: searchInstructorReducer,
  // Add more reducers as needed
});

export default rootReducer;
