import { createSlice } from "@reduxjs/toolkit";
import { toast } from "react-toastify";

// Define initial state here
const initialState = {
  successMessage: "Success",
  errorMessage: "Error",
  infoMessage: "Info",
  warningMessage: "Warning",
};

const toastifySlice = createSlice({
  name: "toastify",
  initialState,
  reducers: {
    successToast: (state, action) => {
      state.successMessage = action.payload || state.successMessage;

      toast.success(state.successMessage); // Display success toast
    },
    errorToast: (state, action) => {
      state.errorMessage = action.payload || state.errorMessage;
      toast.error(state.errorMessage); // Display error toast
    },
    infoToast: (state, action) => {
      state.infoMessage = action.payload || state.infoMessage;
      toast.info(state.infoMessage); // Display information toast
    },
    warnToast: (state, action) => {
      state.warningMessage = action.payload || state.warningMessage;
      toast.warn(state.warningMessage); // Display warning toast
    },
    clearState: (state) => {
      return initialState; // Reset to initial state
    },
  },
});

export const { successToast, errorToast, infoToast, warnToast, clearState } =
  toastifySlice.actions;

export default toastifySlice.reducer;
