// store.js
import { configureStore } from "@reduxjs/toolkit";
import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage"; // defaults to localStorage for web
import rootReducer from "../reducers"; // Your combined reducers

// Redux persist config
const persistConfig = {
  key: "root", // Key to persist your store (root key or specific reducers)
  storage, // Storage method (localStorage in this case)
  whitelist: ["user"], // Only persist the given reducer in this array
};

// Persisted reducer
const persistedReducer = persistReducer(persistConfig, rootReducer);

// Configure store with persisted reducer
const store = configureStore({
  reducer: persistedReducer,
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: false, // Required to avoid issues with non-serializable values in state
    }),
});

export default store;
