import { createSlice } from "@reduxjs/toolkit";

// Define initial state here
const initialState = {
  loading: false,
};

const loaderSlice = createSlice({
  name: "loader",
  initialState,
  reducers: {
    startLoading: (state) => {
      state.loading = true;
    },
    finishLoading: (state) => {
      state.loading = false;
    },
    clearState: (state) => {
      return initialState; // Reset to initial state
    },
  },
});

export const { startLoading, finishLoading, clearState } = loaderSlice.actions;

export default loaderSlice.reducer;
